import React from 'react'
import { Dimensions, FlatList, Pressable, StyleSheet, Text } from 'react-native'
import { RefreshControl } from '../components/RefreshControl'

import { Tile } from '../components/Tile'
import { ArticlesConsumer, ArticlesProvider } from '../context'

const styles = StyleSheet.create({
  flatList: {
    backgroundColor: '#FBFBF8',
    alignItems: 'center',
    justifyContent: 'center',
  },
  empty: { fontWeight: 'bold', marginVertical: 20, color: 'gray' },
})

export interface IListComponentProps {
  navigation: any
  contentTag?: string
  sourceTag?: string
  searchText?: string
  articleIds?: Array<string>
}

export const ListComponent: React.FC<IListComponentProps> = ({
  navigation,
  contentTag,
  sourceTag,
  searchText,
  articleIds,
}) => {
  return (
    <ArticlesProvider
      contentTag={contentTag}
      sourceTag={sourceTag}
      searchText={searchText}
      articleIds={articleIds}
    >
      <ArticlesConsumer>
        {({ articles, isLoading, total, refresh, setCount }) => {
          return (
            <FlatList
              contentContainerStyle={styles.flatList}
              data={articles}
              keyExtractor={(article) => `${article.articleId}`}
              onEndReached={() => {
                if (!articles.length || total <= articles.length) return

                setCount(articles.length)
              }}
              onEndReachedThreshold={0.5}
              refreshControl={
                <RefreshControl
                  refreshing={isLoading}
                  onRefresh={() => {
                    refresh()
                  }}
                />
              }
              renderItem={({ item }) => (
                <Pressable
                  onPress={() => {
                    navigation.navigate('Details', {
                      externalPath: item.url,
                    })
                  }}
                  style={({ pressed }) => ({
                    opacity: pressed ? 0.5 : 1,
                    flex: 1,
                    width: Dimensions.get('screen').width,
                  })}
                >
                  <Tile article={item} />
                </Pressable>
              )}
              ListEmptyComponent={
                <Text style={styles.empty}>
                  Nincs a keresési feltételeknek megfelelő cikk
                </Text>
              }
            />
          )
        }}
      </ArticlesConsumer>
    </ArticlesProvider>
  )
}
