import React, { FC } from 'react'
import { SafeAreaView, StyleSheet, TextInput, View } from 'react-native'

import { ArticleTabParamProps } from '../types'
import { FiltersConsumer } from '../context'
import { ListComponent } from '../components/ListComponent'
import { useDebounce } from '../utils'
import Colors from '../constants/Colors'

const styles = StyleSheet.create({
  safeAreaView: {
    flex: 1,
    backgroundColor: '#FBFBF8',
  },
  sectionStyle: {
    flexDirection: 'row',
    height: 40,
    margin: 10,
  },
  inputStyle: {
    flex: 1,
    color: 'gray',
    paddingLeft: 15,
    paddingRight: 15,
    borderWidth: 1,
    borderRadius: 30,
    borderColor: '#dadae8',
    outlineColor: Colors.orangeWeb,
  },
})
export const SearchScreen: FC<ArticleTabParamProps<'ArticleList'>> = ({
  navigation,
}) => {
  const [searchText, setSearchText] = useDebounce('', 500)
  return (
    <SafeAreaView style={styles.safeAreaView}>
      <View style={styles.sectionStyle}>
        <TextInput
          style={styles.inputStyle}
          onChangeText={(searchText) => setSearchText(searchText)}
          placeholder="Keresés"
          placeholderTextColor="#8b9cb5"
          autoCapitalize="none"
          keyboardType="default"
          returnKeyType="next"
          underlineColorAndroid="#f000"
          blurOnSubmit={false}
        />
      </View>
      <FiltersConsumer>
        {({ sourceTag, contentTag }) => {
          return (
            <ListComponent
              navigation={navigation}
              sourceTag={sourceTag}
              contentTag={contentTag}
              searchText={searchText}
            />
          )
        }}
      </FiltersConsumer>
    </SafeAreaView>
  )
}
