import React, { FunctionComponent, useEffect, useState } from 'react'
import { MultiSelect, Dropdown } from 'react-native-element-dropdown'
import Icon from 'react-native-vector-icons/MaterialIcons'
import { View, Text, StyleSheet } from 'react-native'
import { BottomTabNavigationProp } from '@react-navigation/bottom-tabs'

import Colors from '../constants/Colors'
import { ITag } from '../api/tags/tags.types'
import { SourceTagsConsumer } from '../context'

export interface IDropdownListProps {
  navigation: BottomTabNavigationProp<any, any>
}

const ALL_SOURCES = 'Minden forrás'
const ROBOTO_400REGULAR = 'Roboto_400Regular'
const ROBOTO_300LIGHT = 'Roboto_300Light'
const MAX_HEIGHT = 300

const styles = StyleSheet.create({
  dropdown: {
    height: 24,
    width: 170,
    marginRight: 13,
  },
  placeholder: {
    color: Colors.orangeWeb,
    fontSize: 16,
    marginLeft: 8,
    marginRight: 8,
    maxHeight: 19,
    overflow: 'hidden',
  },
  dropdownContainer: {
    alignSelf: 'flex-start',
    width: 146,
    borderColor: Colors.oxfordBlue,
    borderBottomStartRadius: 10,
    borderBottomEndRadius: 10,
  },
  dropdownContainerTextWrap: {
    flex: 1,
    flexDirection: 'row',
    height: 25,
    margin: 8,
  },
  dropdownContainerText: {
    color: Colors.oxfordBlue,
    fontSize: 14,
  },
  selected: {
    fontFamily: ROBOTO_400REGULAR,
    width: '90%',
  },
  unselected: {
    fontFamily: ROBOTO_300LIGHT,
  },
})

const renderRightIcon = () => (
  <Icon name={'keyboard-arrow-down'} size={24} color={Colors.orangeWeb} />
)

export const DropdownList: FunctionComponent<IDropdownListProps> = ({
  navigation,
}) => {
  const [selected, setSelected] = useState(null)

  useEffect(() => {
    navigation.setParams({ sourceTag: selected })
  }, [selected])

  const renderItem = (item: ITag) => {
    const isSelected = selected === item.value
    return (
      <View style={styles.dropdownContainerTextWrap}>
        <Text
          numberOfLines={1}
          style={[
            styles.dropdownContainerText,
            isSelected ? styles.selected : styles.unselected,
          ]}
        >
          {item.title}
        </Text>
        {isSelected && <Icon name="check" size={16} />}
      </View>
    )
  }

  const onChange = (item: any) => {
    setSelected(selected === item.value ? null : item.value)
  }

  return (
    <SourceTagsConsumer>
      {(sourceTags) => (
        <View>
          <Dropdown
            data={sourceTags || []}
            placeholder={ALL_SOURCES}
            labelField="title"
            valueField="value"
            value={selected}
            onChange={onChange}
            renderRightIcon={renderRightIcon}
            renderItem={renderItem}
            style={styles.dropdown}
            maxHeight={MAX_HEIGHT}
            showsVerticalScrollIndicator={false}
            fontFamily={ROBOTO_400REGULAR}
            placeholderStyle={styles.placeholder}
            selectedTextStyle={styles.placeholder}
            containerStyle={styles.dropdownContainer}
            activeColor={Colors.unselectedBlue + 20}
          />
        </View>
      )}
    </SourceTagsConsumer>
  )
}
