import { tokenPath } from '../../constants/Config'
import { UserActions } from './user.actions'
import { IUserState } from './user.state'

export const userReducer = (
  state: IUserState,
  action: UserActions
): IUserState => {
  switch (action.type) {
    case 'PROFILE_LOAD_SUCCESS':
      return {
        ...state,
        user: action.payload,
      }
    case 'SET_TOKEN': {
      localStorage.setItem(tokenPath, action.payload)
      return {
        ...state,
        token: action.payload,
      }
    }
    default:
      return state
  }
}
