import * as React from 'react'
import { FunctionComponent } from 'react'
import { Text, View, StyleSheet, Pressable } from 'react-native'
import { Card } from 'react-native-elements'

import Colors from '../constants/Colors'
import Icon from 'react-native-vector-icons/MaterialIcons'
import { IArticle } from '../api/articles'
import { UserConsumer } from '../context'

export interface ITileProps {
  article: IArticle
}

const styles = StyleSheet.create({
  cardContainer: {
    padding: 0,
    borderRadius: 10,
    margin: 13,
  },
  imageContainer: {
    width: '100%',
    minHeight: '25vh',
  },
  image: {
    width: '100%',
    height: '100%',
    borderTopLeftRadius: 10,
    borderTopRightRadius: 10,
  },
  cardInfoWrapper: {
    margin: 8,
  },
  titleFavoriteContainer: {
    flex: 1,
    flexDirection: 'row',
  },
  tileTitle: {
    width: '85%',
    fontFamily: 'Roboto_500Medium',
    fontSize: 17,
    color: Colors.oxfordBlue,
  },
  favoriteIcon: {
    color: Colors.oxfordBlue,
  },
  iconWrapper: {
    width: '15%',
    alignItems: 'flex-end',
  },
  sourceSentimentContainer: {
    flex: 1,
    flexDirection: 'row',
    marginTop: 12,
    maxHeight: 15,
  },
  source: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_300Light',
    fontSize: 11,
    width: '85%',
    margin: 0,
  },
  sentiment: {
    alignSelf: 'flex-end',
    width: 10,
    height: 10,
    borderRadius: 200,
    backgroundColor: Colors.oxfordBlue,
    marginEnd: 7,
    marginTop: 1,
  },
})

const sentimentColors: Record<string, string> = {
  good: 'green',
  bad: 'red',
  neutral: Colors.orangeWeb,
}

export const Tile: FunctionComponent<ITileProps> = (props) => {
  const { article } = props
  return (
    <UserConsumer>
      {({ user, setBookmarks }) => {
        const index = user.bookmarkedNews.indexOf(article.articleId)
        const isBookmarked = index > -1
        return (
          <Card containerStyle={styles.cardContainer}>
            <Card.Image
              style={styles.image}
              containerStyle={styles.imageContainer}
              source={{ uri: article.thumbnail }}
            ></Card.Image>
            <View style={styles.cardInfoWrapper}>
              <View style={styles.titleFavoriteContainer}>
                <Text style={styles.tileTitle} numberOfLines={3}>
                  {article.title}
                </Text>
                <Pressable
                  style={styles.iconWrapper}
                  onPress={() => {
                    if (index === -1) {
                      setBookmarks([...user.bookmarkedNews, article.articleId])
                    } else {
                      const tags = [...user.bookmarkedNews]
                      tags.splice(index, 1)
                      setBookmarks(tags)
                    }
                  }}
                >
                  <Icon
                    name={isBookmarked ? 'bookmark' : 'bookmark-border'}
                    size={24}
                    style={styles.favoriteIcon}
                  />
                </Pressable>
              </View>
              <View style={styles.sourceSentimentContainer}>
                <Text style={styles.source} numberOfLines={1}>
                  {article.sourceTag?.value} - {article.date}
                </Text>
                <View style={styles.iconWrapper}>
                  <View
                    style={[
                      styles.sentiment,
                      {
                        backgroundColor:
                          sentimentColors[article.sentimentTag.value] ||
                          Colors.orangeWeb,
                      },
                    ]}
                  ></View>
                </View>
              </View>
            </View>
          </Card>
        )
      }}
    </UserConsumer>
  )
}
