import { IUserQuery, ILoginMutation } from './user.types'
import { client } from '..'
import { omitEmpty } from '../../utils'

export async function fetchProfile() {
  try {
    const response = await client.get<IUserQuery>('/api/profile')
    return response.data?.user
  } catch (error) {
    console.error(error)
  }
}

export interface ILoginParams {
  email: string
  activationCode: string
}

export async function login(params: ILoginParams) {
  try {
    const response = await client.post<ILoginMutation>('/api/login', { params })
    return response.data?.token
  } catch (error) {
    console.error(error)
  }
}

export interface IUpdateProfileParams {
  preferredTags?: Array<string>
  bookmarkedNews?: Array<string>
}

export async function updateProfile(params: IUpdateProfileParams) {
  try {
    await client.put<ILoginMutation>('/api/profile', {
      ...omitEmpty(params),
    })
  } catch (error) {
    console.error(error)
  }
}
