import React from 'react'
import { View, Text, StyleSheet } from 'react-native'
import Colors from '../constants/Colors'
import { ContentTagsConsumer, UserConsumer } from '../context'
import { Switch } from 'react-native-switch'
import { PreferredTagsConsumer } from '../context/preferred-tags.context'

const styles = StyleSheet.create({
  containerView: {
    backgroundColor: Colors.white,
    height: window.screen.height - 50,
  },
  view: {
    height: 50,
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
  },
  text: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_400Regular',
    fontSize: 15,
    marginHorizontal: 13,
  },
  switch: {
    marginHorizontal: 13,
  },
})

export default function CategoriesScreen() {
  return (
    <UserConsumer>
      {({ user, setPreferredTags }) => (
        <PreferredTagsConsumer>
          {(tags) => {
            return (
              <ContentTagsConsumer>
                {(content) => {
                  return (
                    <View style={styles.containerView}>
                      {content?.map((contentItem) => {
                        const value = tags
                          .map((tag) => tag.value)
                          .includes(contentItem.value)
                        return (
                          <View style={styles.view} key={contentItem.value}>
                            <Text style={styles.text}>{contentItem.title}</Text>
                            <Switch
                              onValueChange={() => {
                                const index = user.preferredTags.indexOf(
                                  contentItem.value
                                )
                                if (index === -1) {
                                  setPreferredTags([
                                    ...user.preferredTags,
                                    contentItem.value,
                                  ])
                                } else {
                                  const tags = [...user.preferredTags]
                                  tags.splice(index, 1)
                                  setPreferredTags(tags)
                                }
                              }}
                              value={value}
                              backgroundActive={Colors.orangeWeb + 30}
                              backgroundInactive={Colors.oxfordBlue + 30}
                              activeText=""
                              inActiveText=""
                              containerStyle={{ marginHorizontal: 13 }}
                              circleSize={16}
                              barHeight={10}
                              circleActiveColor={Colors.orangeWeb}
                              circleInActiveColor={Colors.oxfordBlue}
                              circleBorderWidth={0}
                              switchWidthMultiplier={1.5}
                              switchLeftPx={8}
                              switchRightPx={8}
                            />
                          </View>
                        )
                      })}
                    </View>
                  )
                }}
              </ContentTagsConsumer>
            )
          }}
        </PreferredTagsConsumer>
      )}
    </UserConsumer>
  )
}
