import * as Factory from 'factory.ts'
import faker from 'faker'
import { IArticle } from './articles.types'
import { tags } from '../../api/tags/tags.db'
import { capitalize } from '../../utils'

const sourceTags = tags.filter(({ type }) => type === 'source')
const contentTags = tags.filter(({ type }) => type === 'content')
const sentimentTags = tags.filter(({ type }) => type === 'sentiment')

export const ARTICLE_NUMBER = 30
export const ARTICLE_PER_PAGE = 10

export const ArticleMock = Factory.Sync.makeFactory<IArticle>({
  articleId: Factory.each(() => faker.datatype.uuid()),
  title: Factory.each(() =>
    capitalize(faker.random.words(1 + faker.datatype.number(10)))
  ),
  thumbnail: Factory.each(
    (seqNum) => `https://picsum.photos/seed/${seqNum * 10}/200/300`
  ),
  sourceTag: Factory.each(() => faker.random.arrayElement(sourceTags)),
  sentimentTag: Factory.each(() => faker.random.arrayElement(sentimentTags)),
  contentTag: Factory.each(() =>
    faker.random.arrayElements(
      contentTags,
      faker.datatype.number({ min: 1, max: 5 })
    )
  ),
  date: Factory.each(() => faker.date.past().toISOString()),
  // url: Factory.each(() => faker.random.words()),
  // url: Factory.each(() => 'https://github.com/facebook/react-native'),
  url: Factory.each(() => 'https://www.youtube.com/embed/tgbNymZ7vqY'),
  isVideo: Factory.each(() => faker.datatype.boolean()),
})

export const articles = ArticleMock.buildList(ARTICLE_NUMBER)
