import { axiosMockAdapterInstance } from '../client'
import { articles } from './articles.db'

axiosMockAdapterInstance.onGet(/\/api\/articles.*/).reply((request) => {
  const { contentTag, sourceTag, searchText, articleIds, offset, limit } =
    request.params
  const whitelist = articleIds || articles.map((a) => a.articleId)

  const filtered = articles
    .filter((article) =>
      article.contentTag
        .map((tag) => tag?.value)
        .join(' ')
        .includes(contentTag || '')
    )
    .filter((a) => a.sourceTag.value.includes(sourceTag || ''))
    .filter((a) =>
      a.title
        .toLocaleLowerCase()
        .includes((searchText || '').toLocaleLowerCase())
    )
    .filter((a) => whitelist.includes(a.articleId))
    .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime())

  return [
    200,
    {
      total: filtered.length,
      articles: [...filtered].splice(offset, limit),
    },
  ]
})
