import { omitEmpty } from '../../utils'
import { IArticleQuery } from './articles.types'
import { client } from '..'

export interface IFetchArticlesParams {
  offset: number
  limit: number
  contentTag?: string
  sourceTag?: string
  searchText?: string
  articleIds?: Array<string>
}

export async function fetchArticles(params: IFetchArticlesParams) {
  const { offset, limit, contentTag, sourceTag, searchText, articleIds } =
    params
  try {
    const response = await client.get<IArticleQuery>('/api/articles', {
      params: omitEmpty({
        offset,
        limit,
        contentTag,
        sourceTag,
        searchText,
        articleIds,
      }),
    })
    return response.data
  } catch (error) {
    console.error(error)
  }
}
