import * as Factory from 'factory.ts'
import faker from 'faker'
import { IUser } from './user.types'
import { tags } from '../tags/tags.db'
import { articles } from '../articles/articles.db'

const contentTags = tags.filter((tag) => tag.type === 'content')

export const UserMock = Factory.Sync.makeFactory<IUser>({
  name: Factory.each(
    () => `${faker.name.firstName()} ${faker.name.lastName()}`
  ),
  email: Factory.each(() => faker.internet.email()),
  phone: Factory.each(() => faker.phone.phoneNumber()),
  preferredTags: Factory.each(() =>
    faker.random
      .arrayElements(
        contentTags,
        faker.datatype.number({ min: 0, max: contentTags.length })
      )
      .map((tag) => tag.value)
  ),
  bookmarkedNews: Factory.each(() =>
    faker.random
      .arrayElements(
        articles,
        faker.datatype.number({ min: 0, max: articles.length })
      )
      .map((article) => article.articleId)
  ),
})

export const user = UserMock.build()
