import { axiosMockAdapterInstance } from '../client'
import { tags } from './tags.db'

axiosMockAdapterInstance.onGet(/\/api\/tags.*/).reply((request) => {
  const { type } = request.params

  const filtered = tags.filter((tag) => tag.type === type)

  return [
    200,
    {
      total: filtered.length,
      tags: [...filtered],
    },
  ]
})
