import { axiosMockAdapterInstance } from '../client'
import { user } from './user.db'

axiosMockAdapterInstance.onGet(/\/api\/profile.*/).reply((request) => {
  return [
    200,
    {
      user,
    },
  ]
})

axiosMockAdapterInstance.onPost(/\/api\/login.*/).reply((request) => {
  return [
    200,
    {
      token: Math.random().toString(36).slice(-5),
    },
  ]
})

axiosMockAdapterInstance.onPut(/\/api\/profile.*/).reply((request) => {
  const data = JSON.parse(request.data || '{}')
  if (data.preferredTags) {
    user.preferredTags = data.preferredTags
  }
  if (data.bookmarkedNews) {
    user.bookmarkedNews = data.bookmarkedNews
  }
  return [200]
})
