import React, { createRef, useState } from 'react'
import {
  Keyboard,
  KeyboardAvoidingView,
  ScrollView,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native'
import Colors from '../constants/Colors'
import { UserConsumer } from '../context'

export default function LoginScreen() {
  const passwordInputRef = createRef<TextInput>()
  const [formState, setFormState] = useState({
    email: '',
    activationCode: '',
    error: '',
  })

  return (
    <UserConsumer>
      {({ login }) => (
        <View style={styles.mainBody}>
          <ScrollView
            keyboardShouldPersistTaps="handled"
            contentContainerStyle={{
              flex: 1,
              justifyContent: 'center',
              alignContent: 'center',
            }}
          >
            <View>
              <KeyboardAvoidingView enabled>
                <View style={styles.sectionStyle}>
                  <TextInput
                    style={styles.inputStyle}
                    onChangeText={(email) =>
                      setFormState({
                        ...formState,
                        email,
                      })
                    }
                    placeholder="Email cím"
                    placeholderTextColor="#8b9cb5"
                    autoCapitalize="none"
                    keyboardType="email-address"
                    returnKeyType="next"
                    onSubmitEditing={() =>
                      passwordInputRef.current &&
                      passwordInputRef.current.focus()
                    }
                    underlineColorAndroid="#f000"
                    blurOnSubmit={false}
                  />
                </View>
                <View style={styles.sectionStyle}>
                  <TextInput
                    style={styles.inputStyle}
                    onChangeText={(activationCode) =>
                      setFormState({
                        ...formState,
                        activationCode,
                      })
                    }
                    placeholder="Aktivációs kód"
                    placeholderTextColor="#8b9cb5"
                    keyboardType="default"
                    ref={passwordInputRef}
                    onSubmitEditing={Keyboard.dismiss}
                    blurOnSubmit={false}
                    secureTextEntry={false}
                    underlineColorAndroid="#f000"
                    returnKeyType="next"
                  />
                </View>
                {!!formState.error && (
                  <Text style={styles.errorTextStyle} numberOfLines={3}>
                    {formState.error}
                  </Text>
                )}
                <TouchableOpacity
                  style={styles.buttonStyle}
                  activeOpacity={0.5}
                  onPress={() => {
                    const { email, activationCode } = formState
                    return login({ email, activationCode })
                  }}
                >
                  <Text style={styles.buttonTextStyle}>Aktiválás</Text>
                </TouchableOpacity>
              </KeyboardAvoidingView>
            </View>
          </ScrollView>
        </View>
      )}
    </UserConsumer>
  )
}

const styles = StyleSheet.create({
  mainBody: {
    flex: 1,
    justifyContent: 'center',
    alignContent: 'center',
  },
  sectionStyle: {
    flexDirection: 'row',
    height: 40,
    marginTop: 20,
    marginLeft: 35,
    marginRight: 35,
    margin: 10,
  },
  buttonStyle: {
    backgroundColor: '#7DE24E',
    //backgroundColor: '#61dafb',
    borderWidth: 0,
    color: '#FFFFFF',
    borderColor: '#7DE24E',
    height: 40,
    alignItems: 'center',
    borderRadius: 30,
    marginLeft: 35,
    marginRight: 35,
    marginTop: 20,
    marginBottom: 25,
  },
  buttonTextStyle: {
    color: '#FFFFFF',
    paddingVertical: 10,
    fontSize: 16,
  },
  inputStyle: {
    flex: 1,
    color: 'gray',
    paddingLeft: 15,
    paddingRight: 15,
    borderWidth: 1,
    borderRadius: 30,
    borderColor: '#dadae8',
    outlineColor: Colors.orangeWeb,
  },
  registerTextStyle: {
    color: '#FFFFFF',
    textAlign: 'center',
    fontWeight: 'bold',
    fontSize: 14,
    alignSelf: 'center',
    padding: 10,
  },
  errorTextStyle: {
    color: 'red',
    textAlign: 'center',
    fontSize: 14,
  },
})
