import React, { FC, useEffect, useReducer, useState } from 'react'
import {
  initialArticleState,
  IArticlesState,
  articleReducer,
} from '../state/articles'
import { useFetchArticles } from '../state/articles/articles.creators'

interface ArticlesContextProps extends IArticlesState {
  refresh: () => void
  setCount: (count: number) => void
}
const initialState = {
  ...initialArticleState,
  refresh: () => {},
  setCount: () => {},
}

const ArticlesContext = React.createContext<ArticlesContextProps>(initialState)

export interface IArticlesProviderProps {
  contentTag?: string
  sourceTag?: string
  searchText?: string
  articleIds?: Array<string>
}

export const ArticlesProvider: FC<IArticlesProviderProps> = (props) => {
  const limit = 10
  const [offset, setOffset] = useState(0)
  const { sourceTag, contentTag, searchText, articleIds } = props
  const [data, dispatch] = useReducer(articleReducer, initialArticleState)
  const fetchArticles = useFetchArticles(dispatch)
  const refresh = () => {
    setCount(0)
    dispatch({ type: 'ARTICLES_RESET_STATE' })
    fetchArticles({
      offset: 0,
      limit,
      sourceTag,
      contentTag,
      searchText,
      articleIds,
    })
  }

  const setCount = (count: number) => {
    setOffset(count)
  }

  useEffect(() => {
    if (offset === 0) return
    fetchArticles({
      offset,
      limit,
      sourceTag,
      contentTag,
      searchText,
      articleIds,
    })
  }, [offset, limit])

  useEffect(refresh, [sourceTag, searchText, articleIds])

  return (
    <ArticlesContext.Provider value={{ ...data, refresh, setCount }}>
      {props.children}
    </ArticlesContext.Provider>
  )
}

export const ArticlesConsumer = ArticlesContext.Consumer
