import * as React from 'react'
import { ActivityIndicator, StyleSheet } from 'react-native'
import { Card } from 'react-native-elements'
import Icon from 'react-native-vector-icons/MaterialIcons'
import { Text, View } from '../components/Themed'
import Colors from '../constants/Colors'
import { getInstalledStatus, isSupportingBrowser } from '../constants/Config'
// import { InstallStatusContext } from '../context'

export const SuccessCard: React.FC<{ show: boolean }> = ({ show }) => {
  if (!show) return null

  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <Icon
          name="check-circle-outline"
          size={24}
          style={styles.successIcon}
        />
        <Text>Sikeres Telepítés!</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          Ön sikeresen telepítette az alkalmazást, kérjük nyissa meg azt, majd
          aktiválja felhasználóját!
        </Text>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export const FailureCard: React.FC<{ show: boolean }> = ({ show }) => {
  if (!show) return null

  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <ActivityIndicator
          color={Colors.oxfordBlue}
          style={styles.failureIcon}
        />
        <Text>Kérjük várjon...</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          Amennyiben pár percen belül nem jelenik meg a telepítési képernyő, úgy
          valószínűleg ön akadályba ütközött. Ennek több oka lehet:
        </Text>
        <View style={styles.list}>
          <Text
            style={styles.listItem}
          >{`\u2022 Az alkalmazás már telepítve van az ön eszközén`}</Text>
          <Text
            style={styles.listItem}
          >{`\u2022 Az ön böngészője nem támogatja az alkalmazás letöltését`}</Text>
        </View>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export const WaitCard: React.FC<{ show: boolean }> = ({ show }) => {
  if (!show) return null

  return (
    <View style={styles.fullView}>
      <ActivityIndicator color={Colors.oxfordBlue} />
    </View>
  )
}

export default function InstallScreen() {
  // const { installStatus } = React.useContext(InstallStatusContext)
  // console.log({ installStatus })
  return (
    <View style={styles.container}>
      <WaitCard show={false} />
      <SuccessCard show={false} />
      <FailureCard show={true} />
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    backgroundColor: 'gray',
  },
  fullView: { flex: 1, justifyContent: 'center', alignItems: 'center' },
  card: {
    width: '80%',
    borderRadius: 20,
  },
  title: {
    fontSize: 20,
    fontWeight: 'bold',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  text: {
    fontSize: 14,
    color: 'gray',
    marginBottom: 5,
  },
  list: {
    marginVertical: 10,
  },
  listItem: {
    fontSize: 14,
    color: 'gray',
    marginBottom: 3,
    marginLeft: 10,
  },
  successIcon: {
    color: '#7DE24E',
    marginRight: 5,
  },
  failureIcon: {
    color: '#FF0000',
    marginRight: 5,
  },
})
